/* eslint-disable @typescript-eslint/no-explicit-any */
import { Component, Input, OnInit } from '@angular/core';
import * as d3 from 'd3';

@Component({
  // eslint-disable-next-line @angular-eslint/component-selector
  selector: 'icom-libs-order_transaction-detail-step',
  templateUrl: './step.component.html',
  standalone: true
})
export class IComLibsOrderTransactionDetailStepComponent implements OnInit {
  protected dataAction: any[];
  protected dataRollback: any[];
  protected svgElement!: d3.Selection<SVGElement, undefined, HTMLElement, any>;
  protected tooltip: any;

  @Input() data: any[];

  constructor() {
    this.data = [];
    this.dataAction = [];
    this.dataRollback = [];
  }


  ngOnInit(): void {
    this.data.forEach((item, index) => {
      if (item && item.op === 'action') {
        this.dataAction.push({ ...item, y: 10, width: 80, height: 50, x: index * 70  });
        return;
      }
      this.dataRollback.push({ ...item, y: 90, width: 80, height: 50, x: index * 70 - 70 });
    });

    this.svgElement = d3.select(".svgStep");
    this.tooltip = d3.select("body").append("div")
      .attr("class", "tooltip")
      .style("opacity", 0);

    this.buildBlockStep();
    this.buildPath();
    this.buildText();
  }

  protected buildBlockStep() {
    this.svgElement.selectAll(".rect-top")
      .data(this.dataAction)
      .enter()
      .append("rect")
      .attr("class", "rect-top")
      .attr("rx", 15)
      .attr("x", (d: { x: any; }) => d.x)
      .attr("y", (d: { y: any; }) => d.y)
      .attr("width", (d: { width: any; }) => d.width)
      .attr("height", (d: { height: any; }) => d.height)
      .attr("fill", (d) => {
        return d.status === 'succeed' ? 'lightgreen' : d.status === 'failed' ? 'red' : '#cccccc';
      })
      .attr("stroke", "#666666")
      .attr("stroke-width", 1)
      .on("mouseover", (event, d) => {
        this.tooltip.transition().duration(500).style("opacity", .9);
        this.tooltip.html(d.url)
          .style("left", (event.pageX - (d.width / 2)) + "px")
          .style("top", (event.pageY - 50) + "px")
      })
      .on("mouseout", (event, d) => {
        this.tooltip.transition().duration(500).style("opacity", 0);
      })

    // V khi ch nht di
    this.svgElement.selectAll(".rect-bottom")
      .data(this.dataRollback)
      .enter()
      .append("rect")
      .attr("class", "rect-bottom")
      .attr("x", (d: { x: any; }) => d.x)
      .attr("y", (d: { y: any; }) => d.y)
      .attr("width", (d: { width: any; }) => d.width)
      .attr("height", (d: { height: any; }) => d.height)
      .attr("fill", (d) => {
        return d.status === 'succeed' ? 'lightgreen' : d.status === 'failed' ? 'red' : '#cccccc';
      })
      .attr("rx", 15)
      .attr("stroke", "#666666")
      .attr("stroke-linecap", "round")
      .attr("stroke-width", 1)
      .on("mouseover", (event, d) => {
        this.tooltip.transition().duration(500).style("opacity", .9);
        this.tooltip.html(d.url)
          .style("left", (event.pageX - (d.width / 2)) + "px")
          .style("top", (event.pageY - 50) + "px")
      })
      .on("mouseout", (event, d) => {
        this.tooltip.transition().duration(500).style("opacity", 0);
      });;
  }

  protected buildPath() {
    this.svgElement
      .selectAll(".path-bottom")
      .data(this.dataRollback.slice(0, -1)) // Loi b hnh cui cng, v khng c mi tn xut pht t n
      .enter()
      .append("path")
      .attr("class", "path-bottom")
      .attr("d", (d: { x: any; width: any; y: number; height: number; }, i: number) => {
        // const dataCurrent = this.dataRollback[i];
        const indexActionFailed = this.dataAction.findIndex(item => item.status === 'failed');
        if (i + 2 > indexActionFailed) {
          return `M${0},${0} L${0},${0}`;
        }
        const nextRect = this.dataRollback[i + 1];
        const x1 = d.x + d.width;
        const y1 = d.y + d.height / 2;
        const x2 = nextRect.x; // im cui ca ng ni
        const y2 = nextRect.y + nextRect.height / 2;
        return `M${x1},${y1} L${x2},${y2}`;
      })
      .attr("stroke", "#666666") // Mu xm cho ng ni
      .attr("stroke-width", 2) //  rng ng ni
      .attr("stroke-dasharray", "5,5") // ng ni dng dash
      .attr("marker-end", "url(#arrow)"); // S dng mi tn

    this.svgElement
      .selectAll(".path-top")
      .data(this.dataAction) // Loi b hnh cui cng, v khng c mi tn xut pht t n
      .enter()
      .append("path")
      .attr("class", "path-top")
      .attr("d", (d: { x: any; width: any; y: number; height: number; }, i: number) => {
        const dataCurrent = this.dataAction[i];
        if (dataCurrent.status === 'failed') { // Noi dong duoi
          const nextRect = this.dataRollback[i - 1];
          const x1 = d.x + d.width / 2;
          const y1 = d.y + d.height;
          const x2 = nextRect?.x + nextRect.width; // im cui ca ng ni
          const y2 = nextRect.y + nextRect.height / 2;
          return `M${x1},${y1} L${x2},${y2}`;
        }
        if (dataCurrent?.status !== 'succeed') {
          return `M${0},${0} L${0},${0}`;
        }
        const nextRect = this.dataAction[i + 1];
        if (!nextRect) {
          return `M${0},${0} L${0},${0}`;
        }
        const x1 = d?.x + d.width;
        const y1 = d.y + d.height / 2;
        const x2 = nextRect?.x; // im cui ca ng ni
        const y2 = nextRect.y + nextRect.height / 2;
        return `M${x1},${y1} L${x2},${y2}`;
      })
      .attr("stroke", "#666666") // Mu xm cho ng ni
      .attr("stroke-width", 2) //  rng ng ni
      .attr("stroke-dasharray", "5,5") // ng ni dng dash
      .attr("marker-end", "url(#arrow)"); // S dng mi tn
  }

  protected buildText() {
    this.svgElement
      .selectAll("text")
      .data([...this.dataAction, ...this.dataRollback])
      .enter()
      .append("text")
      .attr("x", (d: any) => d.x + d.width / 2)
      .attr("y", (d: any) => d.y + d.height / 2)
      .attr("text-anchor", "middle") // Canh gia vn bn
      .attr("dy", "0.35em") // iu chnh v tr dc ca vn bn
      .text((d: any) => `Branch ${d.branch_id}`)
      .on("mouseover", (event, d) => {
        this.tooltip.transition().duration(500).style("opacity", .9);
        this.tooltip.html(d.url)
          .style("left", (event.pageX - (d.width)) + "px")
          .style("top", (event.pageY - 50) + "px")
      })
      .on("mouseout", (event, d) => {
        this.tooltip.transition().duration(500).style("opacity", 0);
      });
  }
}
