import { ChangeDetectorRef, Component, DestroyRef, TemplateRef, ViewChild, inject } from '@angular/core';
import { CommonModule, NgIf } from '@angular/common';
import { lastValueFrom } from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { ActivatedRoute, Router } from '@angular/router';
import { IComLibsServicesProjectCampaignService, IComLibsServicesProjectOrderService } from '@icom/services/projects';
import * as dayjs from 'dayjs';
import { IComLibsOrderTransactionDetailStepComponent } from './step/step.component';
import { NzIconModule } from 'ng-zorro-antd/icon';
import { NzPageHeaderModule } from 'ng-zorro-antd/page-header';
import { NzSpaceModule } from 'ng-zorro-antd/space';
import { NzTabsModule } from 'ng-zorro-antd/tabs';
import { NzTypographyModule } from 'ng-zorro-antd/typography';
import { NzCardModule } from 'ng-zorro-antd/card';
import { ColumnMode, NgxDatatableModule, TableColumn } from '@swimlane/ngx-datatable';
import { TYPE_TEMPLATE_REF } from '@icom/interfaces';

@Component({
  selector: 'icom-libs-order-detail',
  standalone: true,
  imports: [  CommonModule,
    IComLibsOrderTransactionDetailStepComponent,
    NzTypographyModule,
    NzPageHeaderModule,
    NzSpaceModule,
    NzTabsModule,
    NzIconModule,
    NzCardModule,
    NgxDatatableModule,
    NgIf],
  templateUrl: './detail.component.html',
})
export class IComLibsOrderTransactionListComponent {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  protected columns!: TableColumn[];
  protected ColumnMode = ColumnMode;
  protected readonly headerHeight = 50;
  protected readonly rowHeight = 50;
  protected readonly pageLimit = 10;
  protected rows: any[] = [];
  protected dataOrder: any;
  protected idOrder!: string;

  protected destroyRef = inject(DestroyRef);
  @ViewChild('selectRef', { static: true }) selectRef!: TemplateRef<TYPE_TEMPLATE_REF>;

  constructor(
    private router: ActivatedRoute,
    private orderService: IComLibsServicesProjectOrderService,
    private routerLink: Router,
    private cdr: ChangeDetectorRef
    //  private updateBreadcrumbsService: UpdateBreadcrumbsService
  ) { }


  async ngOnInit() {
    this.columns = [
      { prop: 'product_name', width: 400, sortable: false, name: 'Product Name' },
      { prop: 'product_type', width: 250, sortable: false, name: 'Product Type' },
      { prop: 'sku', width: 200, sortable: false},
      { prop: 'target_phone_number', name: 'Phone Number', width: 150, sortable: false},
      { prop: 'quantity' , sortable: false },
      { sortable: false, prop: 'sale_price', name: 'Sale Price'},
      { sortable: false, name: 'Discount', cellTemplate: this.selectRef},
      { prop: 'brand_code' , sortable: false, name: 'Brand' },
    ]
    this.router.queryParams.pipe(takeUntilDestroyed(this.destroyRef)).subscribe(async (params) => {
      if (params) {
        const id = params?.id || '';
        this.idOrder = id;
        const result = await lastValueFrom(this.orderService.detailTransaction(id as string));
        this.dataOrder = result.data;
        this.rows = this.dataOrder?.items || [];
        this.cdr.detectChanges();
      }
    });
  }
}
